//
// Variables
// --------------------------------------------------

// Adding new color to default Bootstrap's $theme-colors map
$theme-colors: () !default;
$theme-colors: map-merge(
  (
    'accent': $accent,
  ),
  $theme-colors
);

// Options
//
// Modifying Bootstrap global options

$enable-shadows: true !default;
$enable-rounded: true !default;
$enable-print-styles: false !default;

// Spacing

$spacer: 1rem !default;

// Add new space to default Bootstrap's $spacers map
$spacers: () !default;
$spacers: map-merge(
  (
    10: (
      $spacer * 10,
    ),
  ),
  $spacers
);

// Body

$body-color: $gray-700 !default;

// Lists

$list-style-color: $accent !default;

// Links

$link-color: $brand-primary !default;
$link-decoration: none !default;
$link-hover-color: darken($link-color, 10%) !default;
$link-hover-decoration: none !default;
$link-transition: color 0.25s ease-in-out !default;
$emphasized-link-hover-darken-percentage: 0% !default;

// Grid breakpoints
//
// Modifying default Bootstrap's $grid-breakpoints map
$grid-breakpoints: () !default;
$grid-breakpoints: map-merge(
  (
    xs: 0,
    sm: 576px,
    md: 768px,
    lg: 992px,
    xl: 1280px,
  ),
  $grid-breakpoints
);

// Grid containers
//
// Modifying default Bootstrap's $container-max-widths map (max-width of .container)
$container-max-widths: () !default;
$container-max-widths: map-merge(
  (
    xl: 1260px,
  ),
  $container-max-widths
);

// Components
//
// Define common padding and border radius sizes and more.

$border-width: 1px !default;
$border-color: $gray-300 !default;
$border-light-color: rgba($white, 0.12) !default;

$border-radius: 0.3125rem !default;
$border-radius-lg: 0.4375rem !default;
$border-radius-sm: 0.1875rem !default;

$box-shadow: 0 0.25rem 0.5625rem -0.0625rem rgba($black, 0.03), 0 0.275rem 1.25rem -0.0625rem rgba($black, 0.05) !default;
$box-shadow-lg: 0 0.3rem 1.525rem -0.375rem rgba($black, 0.1) !default;
$box-shadow-sm: 0 0.125rem 0.3rem -0.0625rem rgba($black, 0.03), 0 0.275rem 0.75rem -0.0625rem rgba($black, 0.06) !default;

$caret-width: 0.275em !default;

$component-active-bg: $brand-primary !default;

// Fonts
//
// Font, line-height, and color for body text, headings, and more.

$path-to-fonts: 'https://fonts.googleapis.com/css?family=Rubik:300,400,500,700&display=swap' !default;

$font-family-sans-serif: 'Rubik', sans-serif !default;
$headings-color: $gray-800 !default;

$font-size-base: 1rem !default;
$font-size-xl: ($font-size-base * 1.625) !default;
$font-size-lg: ($font-size-base * 1.125) !default;
$font-size-md: ($font-size-base * 0.9375) !default;
$font-size-sm: ($font-size-base * 0.875) !default;
$font-size-ms: ($font-size-base * 0.8125) !default;
$font-size-xs: ($font-size-base * 0.75) !default;

$font-sizes: () !default;
$font-sizes: map-merge(
  (
    'xl': $font-size-xl,
    'lg': $font-size-lg,
    'base': $font-size-base,
    'md': $font-size-md,
    'sm': $font-size-sm,
    'ms': $font-size-ms,
    'xs': $font-size-xs,
  ),
  $font-sizes
);

$font-weight-medium: 500 !default;

$line-height-base: 1.5 !default;
$line-height-lg: 1.5 !default;
$line-height-sm: 1.5 !default;

$h6-font-size: $font-size-base * 1.0625 !default;

